import InputBikeId from './InputBikeId';
    export default {
        components: {
            'input-bike-id': InputBikeId
        },
        data() {
            return {
                viewType: 'main',
                submiting: false,
                mode: '',// select_journey
                type: '',
                bike_id: '',
                journey_id: '',
                info: [],
                infoMembers: [
                    {
                        checked: false,
                        desc: '车太重,骑不动'
                    },{
                        checked: false,
                        desc: '二维码脱落'
                    },{
                        checked: false,
                        desc: '把套歪了'
                    },{
                        checked: false,
                        desc: '车铃丢了'
                    },{
                        checked: false,
                        desc: '踏板坏了'
                    },{
                        checked: false,
                        desc: '龙头歪斜'
                    },{
                        checked: false,
                        desc: '刹车失灵'
                    }, {
                        checked: false,
                        desc: '其他'
                    }
                ],
                img: '',
                imgFile: '',
                imgFileFieldName: 'file',
                note: '',
                journeyList:[],
                pullupConfig: {
                    content: '上拉加载更多',
                    downContent: '松开进行加载',
                    upContent: '上拉加载更多',
                    loadingContent: '加载中...'
                }
            }
        },
        computed: {
            disabled() {
                let isDisabled = true;
                if(this.type == 3) {
                    isDisabled = !(this.bike_id && this.info.length);
                } else if (this.type == 30) {
                    isDisabled = !this.note;
                } else {
                    isDisabled = !this.bike_id;
                }
                return isDisabled;
            }
        },
        methods: {
            handleClickScanBtn() {
                if (USER.token) {
                    if (IS_WECHAT) {
                        this.handleScanCode();
                    } else {
                        this.viewType = 'input';
                    }
                } else {
                    this.$router.push({
                        path: '/login'
                    });
                }
            },
            handleScanCode() {
                window.WECHAT_READY.then(() => {
                    wx.scanQRCode({
                        needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
                        scanType: ["qrCode","barCode"], // 可以指定扫二维码还是一维码，默认二者都有
                        success: (res) => {
                            if (res.errMsg == 'scanQRCode:ok') {
                                this.inputBikeId(res.resultStr);
                            }
                        }
                    });
                });
            },
            parseBikeId(bikeId) {
                const match = bikeId.match(/id=(\d+)/);
                if (match) {
                    bikeId = match[1];
                }
                return bikeId;
            },
            inputBikeId(bikeId) {
                this.bike_id= this.parseBikeId(bikeId);
                this.viewType = 'main';
            },
            checkInfo() {
                const info = [];
                this.infoMembers.forEach(function(item) {
                    if (item.checked) {
                        info.push(item.desc);
                    }
                });
                this.info = info;
            },
            // pass in a file
            // pass out a file or blobdata
            compressFile(file) {
                return new Promise((resolve, reject) => {
                    const fileSize = file.size;
                    const size1M = 1024 * 1024;
                    if (fileSize > size1M) {
                        const url = window.URL.createObjectURL(file);
                        const rate = size1M/fileSize;
                        const tmpImg = new Image();
                        tmpImg.onload = () => {
                            const cvn = document.createElement('canvas');
                            const ctx = cvn.getContext('2d');
                            const naturalWidth = tmpImg.naturalWidth;
                            const naturalHeight = tmpImg.naturalHeight;
                            let width, height;
                            if (naturalWidth > naturalHeight) {
                                width = naturalWidth * rate;
                                height = width/naturalWidth * naturalHeight;
                            } else{
                                height = naturalHeight * rate;
                                width = height/naturalHeight * naturalWidth;
                            }
                            cvn.width = width;
                            cvn.height = height;
                            ctx.drawImage(tmpImg, 0, 0, naturalWidth, naturalHeight, 0, 0, width, height);
                            cvn.toBlob((blobData) => {
                                resolve(blobData);
                                URL.revokeObjectURL(url);
                            });
                        }
                        tmpImg.src = url;
                    } else {
                        resolve(file);
                    }
                });
            },
            uploadIfNeeded() {
                return new Promise((resolve, reject) => {
                    if (this.imgFile) {
                        this.compressFile(this.imgFile).then((file) => {
                            const formData = new FormData();
                            formData.append(this.imgFileFieldName, file);
                            this.$http.post(`${UPLOAD_URL}`, formData).then((result) => {
                                this.img = result.data.path;
                                resolve();
                            }).catch((error) => {
                                reject(error);
                            });
                        });
                    } else {
                        resolve()
                    }
                });
            },
            handleSubmit() {
                this.submiting = true;
                this.uploadIfNeeded().then(() => {
                    const data  = {
                        type: this.type,
                        info: this.info.join(','),
                        img: this.img,
                        note: this.note
                    }

                    if(this.bike_id) {
                        data.bike_id = this.bike_id;
                    }
                    if (this.journey_id) {
                        this.journey_id = this.journey_id;
                    }

                    this.$http({
                        method: 'post',
                        url: `${BASE_URL}/user_api/customer_service`,
                        data: data
                    }).then((result) => {
                        this.submiting = false;
                        this.$vux.toast.show({
                            text: '提交成功'
                        });
                        this.$router.replace({
                            path: '/'
                        });
                    }).catch((e) => {
                        this.submiting = false;
                        this.$vux.toast.show({
                            type: 'warn',
                            text: e.response.data.msg || '提交失败, 请重试'
                        });
                    });
                }).catch((error) => {
                    console.log(error);
                    this.submiting = false;
                    this.$vux.toast.show({
                        text: '提交失败, 请重试'
                    });
                })
            }
        },
        created() {
            this.type = this.$route.query.type || '';
            this.bike_id = this.$route.query.bike_id || '';
            this.journey_id = this.$route.query.journey_id || '';
        }
    }



