export default {
    props: ['title'],
    data() {
        return {
            bike_id: '7550000005',
            disabled: true
        }
    },
    methods: {
        handleBack() {
            this.$emit('back');
        },
        submit() {
            this.$emit('submit', this.bike_id);
            this.bike_id = '';
        }
    }
}



